<template>
  <div id="app">
    <Picker :visible="showPicker" :indexs="pickerIndex" :data="pickerContent" @change="pickChange">
      <template slot-scope="props">
        <span class="itemfz">{{props.item.name}}</span>
      </template>
    </Picker>
    <p>{{val}}</p>
  </div>
</template>

<script>
import Picker from './mfw-picker/Picker'
export default {
  name: 'App',
  data() {
    return {
      showPicker: true,
      pickerIndex: 2,
      pickerContent: [],
      val: ''
    }
  },
  components: {
    Picker
  },
  created() {
    // 第一个picker  data
    let num = 20
    let year = 2000
    while (num--) {
      this.pickerContent.push({
        name: `${year++}年`
      })
    }
  },
  methods: {
    pickChange(d) {
      console.log(d)
      this.val = d
    }
  }
}
</script>
<style>
</style>



